.container {
	perspective: 500px;
	perspective-origin: 50% 50%;
}

.cube, .cube .face, .face .face-back {
	height: 80px;
	width: 80px;
}

.cube {
	transform-style: preserve-3d;
	transform: rotateX( 0deg ) rotateY( -30deg );
	margin: 150px auto;
	animation: giro 20s infinite linear;
}
.cube:hover{
	animation: giro 2s infinite linear;
}

.cube .face {
	position: absolute;
	border: 1px solid linear-gradient(74deg, rgba(42,36,36,1) 4%, rgba(165,176,26,1) 95%);
	border-radius: 9px;
}

.cube .face .img {
	border-radius: 9px;
}

.cube .face-back{
	background: #000;
	position: absolute;
	border: 1px solid linear-gradient(74deg, rgba(42,36,36,1) 4%, rgba(165,176,26,1) 95%);
	border-radius: 9px;

}

/*face front*/
.cube .face:nth-child(1) {
	transform: translateZ(40px);
}
/*face back*/
.cube .face:nth-child(2) {
	transform: rotateY(180deg) translateZ(40px);
}
/*Cara left*/
.cube .face:nth-child(3) {
	transform: rotateY(-90deg) translateZ(40px);
}
/*face right*/
.cube .face:nth-child(4) {
	transform: rotateY(90deg) translateZ(40px);
}
/*face up*/
.cube .face:nth-child(5) {
	transform: rotateX(90deg) translateZ(40px);
}
/*face down*/
.cube .face:nth-child(6) {
	transform: rotateX(-90deg) translateZ(40px);
}


/************feces backs*************/
/*face front*/
.cube .face-back:nth-child(7) {
	transform: translateZ(39px);
}
/*face back*/
.cube .face-back:nth-child(8) {
	transform: rotateY(180deg) translateZ(39px);
}
/*face left*/
.cube .face-back:nth-child(9) {
	transform: rotateY(-90deg) translateZ(39px);
}
/*face right*/
.cube .face-back:nth-child(10) {
	transform: rotateY(90deg) translateZ(39px);
}
/*face up*/
.cube .face-back:nth-child(11) {
	transform: rotateX(90deg) translateZ(39px);
}
/*face down*/
.cube .face-back:nth-child(12) {
	transform: rotateX(-90deg) translateZ(39px);
}

/*faces interns to convine the cross in the middle*/
.cube .face-back:nth-child(13) {
	transform: rotateX(180deg);
}

.cube.face-back:nth-child(14) {
	transform: rotateY(90deg);
}

.cube .face-back:nth-child(15) {
	transform: rotateX(90deg);
}

@keyframes giro {
    0% { transform: rotateX(-45deg) rotateY(0deg); }
    50% { transform: rotateX(45deg) rotateY(360deg); }
    100% { transform: rotateX(-45deg) rotateY(720deg); }
}

@keyframes giro360 {
    0% { transform: rotateX(  0deg) rotateY(  0deg); }
  100% { transform: rotateX(360deg) rotateY(360deg); }
}

